.header {
  position: relative;
  background: url("/img/header-bg.png") no-repeat center;
  background-size: cover;
  &::before {
    @include horizontal-center;
    bottom: 30px;
    content: "";
    width: 290px;
    height: 334px;
    background: url("/img/");
  }
  &__wrapper {
    @include container;
    padding: 97px 15px 353px;
  }
  &__navigation {
    margin-bottom: 64px;
  }
  &__title {
    margin: 0 0 23px;
    color: rgb(255, 255, 255);
    font-family: $main-font;
    font-size: 22px;
    font-weight: 400;
    line-height: 30px;
    letter-spacing: 1.32px;
    text-align: center;
    strong {
      color: rgb(0, 253, 254);
      font-weight: 500;
      text-transform: uppercase;
    }
  }
  &__text {
    text-align: center;
  }
}
