.social {
  transform: rotate(-45deg);
  &__link {
    position: relative;
    display: block;
    width: 32px;
    height: 32px;
    background-color: white;
    border: 1px solid rgb(70, 100, 188);
    border-radius: 5px;
    box-sizing: border-box;
    &:not(:last-of-type) {
      margin-right: 16px;
    }
    &::before {
      position: absolute;
      content: "";
      top: 5px;
      right: 10px;
    }
    svg {
      fill: red;
    } // &--facebook {
    //   width: 11px;
    //   height: 21px;
    // }
  }
}
